.hero {
  display: flex;
  justify-content: space-around;
}

.hero h1 {
  font-family: "Poppins", sans-serif;
  width: 50%;
  position: relative;
  top: 6rem;
}

.hero img {
  box-shadow: 0 0 3px rgb(0, 0, 0.3);
  position: relative;
  top: 3rem;
  width: 30%;
  height: 63vh;
  border-radius: 50%;
  cursor: pointer;
  animation: imgUp 0.7s linear infinite alternate;
  user-select: none;
}

@keyframes imgUp {
  to {
    transform: translateY(-10px);
  }
}

.btn button {
  padding: 20px 50px;
  display: flex;
  margin-top: -10px;
  margin-left: 4rem;
  background-color: rgb(134, 16, 124);
  color: #fff;
  font-size: 1.2rem;
  border-radius: 7px;
  border: 0;
  outline: 0;
  transition: 0.5s ease-in;
  cursor: pointer;
  overflow: hidden;
  font-family: cursive;
}

.btn button:hover {
  background-color: rgb(38, 5, 33);
}

@media only screen and (max-width: 600px) {
  .hero {
    flex-direction: column-reverse;
  }

  .hero img {
    width: 90%;
    height: 47vh;
    margin-left: 22px;
  }

  .hero h1 {
    width: 90%;
    margin-left: 24px;
  }

  .btn button {
    display: flex;
    position: relative;
    top: 10rem;
    left: 2rem;
  }
}
