form {
  position: relative;
  top: 4rem;
}
.main-div {
  display: flex;
  justify-content: center;
}

label {
  display: flex;
  font-size: 1.2rem;
  font-weight: 600;
}

input {
  border-radius: 3px;
  width: 100%;
  position: relative;
  font-weight: 700;
  padding: 10px;
  outline: 0;
  border: 0;
  border: 1px solid gray;
}

.headings {
  display: flex;
  position: relative;
  left: 9rem;
}

.mess label {
  display: flex;
  flex-direction: column;
}

.mess input {
  padding: 25px;
  border-radius: 3px;
}

button {
  padding: 12px 30px;
  position: relative;
  top: 1.2rem;
  transition: 0.6s ease-in;
}

button:hover {
  background-color: rgb(106, 27, 215);
  outline: 0;
  border: 0;
  border-radius: 3px;
  color: #fff;
}

@media only screen and (max-width: 600px) {
  input {
    top: -5rem;
    width: 70%;
    position: relative;
    left: 2rem;
  }
.main-div{
    position: relative;
    top: 7rem;
    display: flex;
    flex-direction: column;
}
  label {
    position: relative;
    left: 2rem;
    top: -5rem;

  }

  button {
    top: -3rem;
    position: relative;
    left: 2rem;
    padding: 20px 60px;
    font-size: 1.2rem;
  }

  .mess input {
    top: 1rem;
    padding: 30px;
    position: relative;
    left: -2px;
  }

  
}
