@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100&family=Raleway:wght@300&family=Roboto:ital,wght@0,100;1,100&display=swap');

*{
    margin: 0;padding: 0;
}

body{
    font-family: 'Poppins', sans-serif;

}

nav {
    display: flex;
    background-color: rgb(247, 244, 244);
    box-shadow:  0 0 10px rgb(0, 0, 0.2);
    z-index: 100;
    position: sticky;
    top: 0;

}

main{
    display: flex;
}

main a{
    font-family: 'Poppins', sans-serif;
    padding: 20px;
    color: #ca11c4;
    text-decoration: 0;
    font-size: 20px;
    position: relative;
    left: 12rem;
    font-weight: 900;
}

main  h1{
    font-weight: 900;
    color: #070404;
    margin-top: 1rem;
    margin-left: 6rem;
    cursor: pointer;
}

main a:hover{
    color: rgb(24, 3, 22);
}

@media only screen and (max-width: 600px){
    nav{
        flex-direction: column;
        display: grid;
    }

    main{
        display: flex;
        flex-direction: column;
        height: 30vh;
    }

    main a{
        padding: 5px 0px;
        color: rgb(0, 0, 0);
        font-weight: 900;
        font-size: 1.6rem;
    }
}

